.header {
  background: #336CB1;
  color: white;
  font-size: 300%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 2%;
  stroke: white;
  border-bottom: 1px solid black;
}

.header > * {
  padding-left: 14px;
  padding-right: 14px;
}
.header > p {
  margin-top: 3%;
  margin-bottom: 3%;
  padding-top: 1%;
  text-align: center;
}


.replay {
  -ms-flex-item-align: end;
      align-self: flex-end;
  min-width: 3%;
  padding: 0;
  margin: 0;
  opacity: 0.5;
  -webkit-transition: opacity 200ms 200ms;
  -o-transition: opacity 200ms 200ms;
  transition: opacity 200ms 200ms;
}
.replay:hover {
  opacity: 1.0;
  -webkit-transition: opacity 200ms 200ms;
  -o-transition: opacity 200ms 200ms;
  transition: opacity 200ms 200ms;
}



.peterpeterblue, .johnjohnblue, .cliffordcliffordblue, .evansevansblue {
  color: #F4B235;
  stroke: #F4B235;
  /*transition: color 200ms 200ms, stroke 200ms 200ms;*/
}
.whitepeterblue, .whitejohnblue, .whitecliffordblue, .whiteevansblue {
  color: white;
  stroke: white;
  /*transition: color 200ms 200ms, stroke 200ms 200ms;*/
}

@media only screen and (max-width: 500px) {
    .header {
      -ms-flex-direction: column;
          flex-direction: column;
      -ms-flex-align: center;
          align-items: center;
      -ms-flex-pack: center;
          justify-content: center;
    }
    .middles {
      display: none;
    }
}



